.sortingBox {
  width: 100%;
  background-color: var(--mainColorDarker);
  height: 76%;
  background: linear-gradient(145deg, #24282f, #2b2f38);
  box-shadow: inset 9px 9px 17px #1d2025, inset -9px -9px 17px #333843;
  border-radius: 10px;
  display: flex;
  justify-content: space-evenly;
  .hey {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-evenly;

    .bar {
      width: 5rem;
      height: 70%;
      background-color: var(--mainColorDarker);
      border-radius: 0px 0px 5px 5px;
      box-shadow: 7px 7px 14px #191c21, -7px -7px 14px #292e35;
      padding-left: 0.7rem;
      padding-right: 0.7rem;
      padding-bottom: 0.7rem;
      transform-origin: top;
      .hollow {
        width: 100%;
        height: 100%;
        box-shadow: inset 7px 7px 14px #191c21, inset -7px -7px 14px #292e35;
        border-radius: 0px 0px 5px 5px;
        padding-left: 0.5rem;
        padding-right: 0.5rem;
        padding-bottom: 0.1rem;
        .fill {
          box-shadow: var(--gradientOrangeShadow);
          background: var(--gradientOrange);
          width: 100%;
          height: 100%;
          border-radius: 0px 0px 5px 5px;
          display: flex;
          justify-content: center;
          p {
            margin-top: 2rem;
            font-size: 1.5rem;
            font-weight: 600;
            color: var(--mainColorDarker);
          }
        }
      }
    }
  }
}
